import { Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
// Service
import { Menu, MenuService } from 'src/app/core/services/menu.service';
import { LoginService } from 'src/app/core/services/login.service';
import { ToastService } from 'src/app/core/services/toast.service';
import {
  trigger,
  state,
  style,
  transition,
  animate,
} from '@angular/animations';
@Component({
  selector: 'app-side-nav',
  templateUrl: './side-nav.component.html',
  styleUrls: ['./side-nav.component.scss'],
  animations: [
    trigger('submenu', [
      state(
        'hidden',
        style({
          height: '0',
          overflow: 'hidden',
        })
      ),
      state(
        'visible',
        style({
          height: '*',
        })
      ),
      transition('visible <=> hidden', [
        style({ overflow: 'hidden' }),
        animate('{{transitionParams}}'),
      ]),
      transition('void => *', animate(0)),
    ]),
  ],
})
export class SideNavComponent implements OnInit {
  @Input() isMenuVisible: boolean = false;
  menus: Menu[] = [];
  errorMessage = '';

  constructor(
    private menuService: MenuService,
    private loginService: LoginService,
    private toastService: ToastService,
    private router: Router
  ) {}

  ngOnInit() {
    const url = this.router.routerState.snapshot.url;
    console.log(url);
    this.menuService.getMenus().subscribe({
      next: (menus) => {
        this.menus = menus;
        localStorage.setItem('menus', JSON.stringify(menus));
        if (menus == null || menus.length == 0) {
          this.toastService.error('message.no-role-permission');
          this.loginService.logout();
        }
        if (!this.loginService.checkPermission(url)) {
          this.toastService.error('message.no-function-permission');
        }
      },
      error: (error) => {
        console.log('sideNav>getMenus()', error);
        this.toastService.error('message.no-function-permission');
        this.loginService.logout();
        this.router.navigate(['/redirect']);
      },
    });
  }

  toggleMenu() {
    this.menuService.toggleMenuBar.next(true);
  }
}
