import { Component, OnInit, OnDestroy } from '@angular/core';
import { MediaObserver, MediaChange } from '@angular/flex-layout';
import { Router, NavigationStart, RouterEvent } from '@angular/router';
import { Subscription, Observable } from 'rxjs';
import { filter, map } from 'rxjs/operators';
// Service
import { BasicService } from '../../core/services/basic.service';
import { MenuService } from 'src/app/core/services/menu.service';
import { ApplicationStateService } from 'src/app/core/services/application-state.service';

@Component({
  selector: 'app-layout',
  templateUrl: './layout.component.html',
  styleUrls: ['./layout.component.scss'],
})
export class LayoutComponent implements OnInit, OnDestroy {
  isMenuVisible: boolean = false;
  showLoading = false;
  sideNavOpened: boolean = false;
  sideNavMode: 'side' | 'over' = 'side';
  toolBarHeight = 64;
  private readonly mediaWatcher: Subscription;
  title = '';
  filename = '';
  navStart: Observable<NavigationStart> = new Observable<NavigationStart>();
  constructor(
    media: MediaObserver,
    private readonly basicService: BasicService,
    private menuService: MenuService,
    private applicationStateService: ApplicationStateService,
    private router: Router
  ) {
    // Create a new Observable that publishes only the NavigationStart event
    router.events
      .pipe(filter((evt) => evt instanceof RouterEvent))
      .subscribe((evt: any) => {
        this.title = this.basicService.getMenuName(evt.url);
      });

    this.mediaWatcher = media
      .asObservable()
      .pipe(
        filter((changes: MediaChange[]) => changes.length > 0),
        map((changes: MediaChange[]) => changes[0])
      )
      .subscribe((change: MediaChange) => {
        if (change.mqAlias === 'sm' || change.mqAlias === 'xs') {
          if (this.sideNavOpened) {
            this.sideNavOpened = false;
          }
          this.sideNavMode = 'over';
        } else {
          this.sideNavOpened = true;
          this.sideNavMode = 'side';
        }
        if (change.mqAlias === 'xs') {
          this.toolBarHeight = 56;
        } else {
          this.toolBarHeight = 64;
        }
      });
  }

  ngOnInit() {
    let that = this;
    this.menuService.toggleMenuBar.subscribe(function (data: any) {
      if (data && data != null) {
        that.isMenuVisible = !that.isMenuVisible;
      }
    });

    if (this.applicationStateService.getIsMobileResolution()) {
      this.isMenuVisible = false;
    } else {
      this.isMenuVisible = true;
    }
  }

  ngOnDestroy() {
    this.menuService.toggleMenuBar.observers.forEach(function (element) {
      element.complete();
    });
  }
}
