import { Injectable, Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { LoginService } from './login.service';
import { TranslateService } from '@ngx-translate/core';
import { LoaderService } from 'src/app/core/services/loader.service';
import { childRoutes } from '../../pages/pages-routing-config.const';
import { environment } from 'src/environments/environment';
export interface Page {
  totalPages: number;
  totalElements: number;
  size: number;
  number: number;
  numberOfElements: number;
  last: boolean;
  first: boolean;
  empty: boolean;
}
export interface PageData {
  page: Page;
  content: any[];
}
@Injectable()
export class BasicService {
  @Output() loading: EventEmitter<boolean> = new EventEmitter();
  private baseUrl = environment.apiUrl;
  constructor(
    private router: Router,
    private translate: TranslateService,
    private loginService: LoginService,
    private loaderService: LoaderService
  ) {}

  emitLoading(show: boolean) {
    this.loading.emit(show);
  }

  getMenuName(url: string) {
    let title = '';
    childRoutes.forEach((item) => {
      if (url == '/') {
        title = 'Welcome';
      } else if (url == '/401') {
        title = '';
      } else if (
        url == '/' + item.path ||
        url.startsWith('/' + item.path + '/')
      ) {
        title = item.data.title;
      }
    });
    return title;
  }

  getI18n(i18n: string) {
    return this.translate.instant(i18n);
  }

  showTotalRecords(records: number) {
    const message = this.translate.instant('messages.total-records', {
      records,
    });
    this.showMessage(message);
  }

  showNoDataFound() {
    const message = this.getI18n('messages.no-data-found');
    this.showMessage(message);
  }

  showErrorMessageByKey(key: string) {
    let message = this.translate.instant(key);
    this.showError(message);
  }

  showError(error: any) {
    this.loaderService.hide();
    //403
    if (error.status == 403) {
      this.loginService.logout();
      this.router.navigate(['/redirect']);
    } else if (error.status == 401) {
      this.router.navigate(['/401']);
    }
  }

  showMessage(message: string) {
    console.log(message);
  }
}
